.info-contacts {
	margin-top: 30px;
	margin-bottom: 30px;
	&__wrap {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center; }
	&__wrap h1 {
		font-size: 18px; }
	&__wrap p {
		font-size: 14px;
		margin: 0;
		margin-top: 7px; }
	&__wrap a {
		text-decoration: none;
		font-size: 14px;
		color: #413D43;
		margin: 0;
		margin-top: 7px;
		margin-right: 10px; }
	&__info {
		width: 290px;
		margin: auto; }
	&__wrap div {
		margin-top: 40px; }
	&__address h1 {
		background: url(../img/marker.png) no-repeat 3px center;
		background-size: 20px;
		padding-left: 30px; }
	&__time h1 {
		background: url(../img/clock.png) no-repeat 2px center;
		background-size: 20px;
		padding-left: 30px; }
	&__phone-block h1 {
		background: url(../img/phone-call.png) no-repeat 2px center;
		background-size: 20px;
		padding-left: 30px; }
	&__email-block h1 {
		background: url(../img/email.png) no-repeat 0 center;
		background-size: 25px;
		padding-left: 30px; } }

.info-contacts__question {
	background: #FFEFEA;
	border-radius: 10px;
	width: 290px;
	margin: auto; }

.question {
	&__wrap {
		padding: 20px;
		padding-top: 30px;
		padding-bottom: 30px;
		display: flex;
		flex-direction: column; }
	&__wrap h1 {
		margin-bottom: 20px; }
	&__wrap p {
		font-size: 14px;
		margin-top: 10px; }
	&__wrap div {
		margin-top: 40px; }
	&__input {
		font-size: 12px;
		padding: 13px 20px;
		width: 100%;
		background: transparent;
		border-radius: 50px;
		border: 1px solid #B9B9B9;
		margin-bottom: 10px; }
	&__input::placeholder {
		font-size: 12px; }
	&__wrap textarea {
		font-size: 12px;
		border: none;
		background: transparent;
		border: 1px solid #B9B9B9;
		border-radius: 10px;
		padding: 10px 20px;
		resize: none;
		height: 95px; }
	&__btn {
		font-size: 12px;
		margin-top: 20px;
		color: #fff;
		border: none;
		padding: 17px 20px;
		width: 100%;
		background: #442751;
		border-radius: 50px;
		cursor: pointer; }
	&__btn:hover,
	&__btn:focus {
		font-weight: 500;
		box-shadow: 0 0 15px rgba(#000, .1); }
	&__btn:active {
		font-weight: 400;
		box-shadow: none; }
	&__wrap label {
		display: flex;
		margin-top: 10px;
		position: relative; }
	&__wrap label p {
		margin: 0;
		padding-left: 30px;
		font-size: 12px;
		color: rgba(#1D3B3C, .55); }
	&__wrap label p span {
		font-weight: 500;
		text-decoration: underline; }
	&__wrap input[type="checkbox"] + span {
		position: absolute;
		top: 3px;
		left: 0;
		width: 24px;
		height: 24px;
		border-radius: 50%;
		border: 1px solid #442751; }
	&__wrap input[type="checkbox"]:checked + span:before {
		content: '';
		position: absolute;
		top: calc(50% - 8px);
		left: calc(50% - 8px);
		width: 16px;
		height: 16px;
		border-radius: 50%;
		background: #442751; } }

@media (min-width: 800px) {
	.info-contacts {
		margin-top: 50px;
		margin-bottom: 90px;
		&__wrap {
			flex-direction: row;
			align-items: flex-start;
			justify-content: space-between; }
		&__info {
			background: #FFFFFF;
			border-radius: 10px;
			padding-left: 20px;
			padding-right: 50px;
			width: auto;
			max-width: 937px;
			margin: 0;
			margin-right: 10px; } }
	.info-contacts__question {
		width: auto;
		max-width: 599px;
		margin: 0;
		margin-left: 10px; }

	.question {
		&__wrap {
			padding: 30px 50px; }
		&__wrap h1 {
			margin-bottom: 25px; }
		&__input {
			font-size: 18px;
			padding: 18px 40px; }
		&__input::placeholder {
			font-size: 18px; }
		&__wrap textarea {
			font-size: 18px;
			padding: 20px 40px;
			height: 127px; }
		&__btn {
			font-size: 18px;
			padding: 18px 20px; }
		&__wrap label {
			margin-top: 15px; } } }

@media (min-width: 1000px) {
	.info-contacts {
		&__wrap {}
		&__wrap h1 {
			font-size: 30px; }
		&__wrap p {
			font-size: 18px; }
		&__wrap a {
			font-size: 18px;
			display: inline-block;
			margin-top: 10px; }
		&__phone {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between; }
		&__my-info {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			max-width: 723px; }
		&__my-info h1 {
			width: 100%; }
		&__my-info p {
			margin-right: 10px; } } }

@media (min-width: 1500px) {
	.info-contacts {
		&__info {
			padding-right: 150px; } } }
