.popular-goods {
	&__slider {
		display: flex;
		flex-direction: column; }
	&__top {
		display: flex;
		flex-direction: column-reverse;
		justify-content: space-between; }
	&__top h1 {
		display: none;
		text-align: center;
		margin: 10px 0; }
	&__controls {
		display: flex;
		flex-direction: column;
		align-items: center; }
	&__controls label {
		width: 290px;
		text-align: center;
		font-size: 16px;
		padding: 20px 20px;
		padding-left: 30px;
		border: 2px solid #7A5A88;
		border-radius: 5px;
		margin: 5px 0;
		cursor: pointer; }
	&__controls label:first-child {
		background: #EBDDF1 url(../img/popular-goods/popular.png) no-repeat 25px center; }
	&__controls label:nth-child(2) {
		background: #EBDDF1 url(../img/popular-goods/new.png) no-repeat 72px center; }
	&__controls label:last-child {
		background: #EBDDF1 url(../img/popular-goods/discount.png) no-repeat 78px center; }
	&__controls label:hover,
	&__controls label:focus {
		background-image: url(../img/popular-goods/popular-active.png);
		background-color: #7A5A88;
		color: #fff; }
	&__controls label:active {
		background-color: #442751;
		color: #fff; } }

#popularProducts,
#newProducts,
#discountsProducts {
	display: none; }

#popularProductsToggle:checked ~ .popular-goods__wrapper #popularProductsTitle,
#newProductsToggle:checked ~ .popular-goods__wrapper #newProductsTitle,
#discountsProductsToggle:checked ~ .popular-goods__wrapper #discountsProductsTitle {
	display: flex; }
#popularProductsToggle:checked ~ .popular-goods__wrapper #popularProducts,
#newProductsToggle:checked ~ .popular-goods__wrapper #newProducts,
#discountsProductsToggle:checked ~ .popular-goods__wrapper #discountsProducts {
	display: flex; }
#popularProductsToggle:checked ~ .popular-goods__wrapper label[for="popularProductsToggle"] {
	background: #442751 url(../img/popular-goods/popular-active.png) no-repeat 25px center;
	color: #fff; }
#newProductsToggle:checked ~ .popular-goods__wrapper label[for="newProductsToggle"] {
	background: #442751 url(../img/popular-goods/popular-active.png) no-repeat 72px center;
	color: #fff; }
#discountsProductsToggle:checked ~ .popular-goods__wrapper label[for="discountsProductsToggle"] {
	background: #442751 url(../img/popular-goods/popular-active.png) no-repeat 78px center;
	color: #fff; }

@media (min-width: 900px) {
	.popular-goods {
		&__top {
			flex-direction: column;
			align-items: center; }
		&__top h1 {
			text-align: left;
			margin: 15px 0; }
		&__controls {
			flex-direction: row; }
		&__controls label {
			width: 260px;
			padding: 20px 0px;
			padding-left: 40px;
			margin: 10px 5px; } } }

@media (min-width: 1300px) {
	.popular-goods {
		margin-top: 120px;
		&__top {
			flex-direction: row;
			align-items: center; }
		&__controls {
			flex-direction: row; } } }
