.certificate {
	margin-top: 40px;
	margin-bottom: 40px;
	&__wrap {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		text-align: center; }
	&__wrap p {
		font-size: 14px;
		max-width: 683px; }
	&__text {
		margin-bottom: 30px; }
	&__btn {
		text-decoration: none;
		font-size: 16px;
		color: #442751;
		padding: 20px 60px;
		border: 1px solid #442751;
		border-radius: 50px;
		background: transparent; }
	&__btn:hover,
	&__btn:focus {
		font-weight: 500;
		background: #442751;
		color: #fff;
		box-shadow: 0 0 15px rgba(#000, .1); }
	&__btn:focus {
		font-weight: 400;
		background: #513261;
		colro: #fff;
		box-shadow: none; } }

#certificate-carousel img {
	width: 199px;
	margin: auto; }

#certificate-carousel .owl-dots .owl-dot span {
	background: #C4C4C4; }

#certificate-carousel .owl-dots .active span {
	background: #442751; }

@media (min-width: 900px) {
	.certificate {
		margin-top: 140px;
		margin-bottom: 140px;
		&__wrap {
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			align-items: flex-start;
			justify-content: space-between;
			text-align: left; }
		&__wrap #certificate-carousel {
			order: 2;
			margin-top: 60px; }
		&__details {
			order: 1;
			display: flex;
			flex-direction: column;
			align-items: flex-end; }
		&__wrap p {
			font-size: 18px; }
		&__btn {
			font-size: 18px; } } }

#certificate-carousel img {
	width: 390px; }
