.partners {
	margin-top: 40px;
	&__wrap {
		text-align: center {
    position: relative; } } }

@media (min-width: 900px) {
	.partners {
		margin-top: 120px;
		&__wrap {
			text-align: left; } } }

#partners-carousel .owl-item {
  clear: both;
  display: inline-block;
  float: none;
  text-align: center;
  vertical-align: middle; }

#partners-carousel .owl-item img {
  display: inline-block;
  max-width: 100%;
  height: auto;
  width: auto; }
