.header {
	&__wrap {
		margin-top: 15px; }
	&__wrap-1 {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__wrap img {
		height: 40px; }
	&__form-search {
		display: none;
		justify-content: center;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: #FBFDFF;
		z-index: 100; }
	&__form-search span {
		position: absolute;
		top: 17px;
		left: 5%;
		width: 30px;
		height: 30px;
		background: transparent;
		border: none; }
	&__form-search span:before,
	&__form-search span:after {
		content: '';
		position: absolute;
		top: 10px;
		width: 28px;
		height: 2px;
		background: #000; }
	&__form-search span:before {
		left: 0;
		transform: rotate(45deg); }
	&__form-search span:after {
		left: 0;
		transform: rotate(-45deg); }
	&__form-search input {
		margin-top: 65px; }
	&__form-search input[type='search'] {
		width: 70%;
		height: 53px;
		border: 1px solid #E1DAE5;
		font-size: 14px;
		color: #8E8E8E;
		padding-left: 20px; }
	&__form-search input[type='search']:hover,
	&__form-search input[type='search']:focus {
		border: 1px solid #C5B1D0; }
	&__form-search input[type='submit'] {
		width: 20%;
		height: 53px;
		font-size: 0;
		border: none;
		background: #442751 url(../img/arrow.png) no-repeat center;
		transform: rotate(180deg); }
	&__form-search-active {
		display: flex; }
	&__basket {
		font-size: 0; }
	&__basket span {
		padding: 20px;
		background: #442751 url(../img/bag-white.png) no-repeat center;
		background-size: 20px;
		border-radius: 50%; }
	&__basket:hover span,
	&__basket:focus span {
		background: #442751 url(../img/bag-white.png) no-repeat center; }
	&__basket:active span {
		background: #513261 url(../img/bag-white.png) no-repeat center; }
	&__wrap-2 {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 15px; }
	&__btn {
		background: transparent;
		border: 1px solid #442751;
		border-radius: 5px;
		padding: 12px 0;
		font-size: 14px;
		padding-left: 33px;
		padding-right: 17px;
		cursor: pointer; }
	&__btn--catalog {
		background: #442751 url(../img/burger.png) no-repeat 10px center;
		color: #fff; }
	&__btn--search {
		background: url(../img/search-mobile.png) no-repeat 14px 10px; }
	&__menu {
		display: none;
		flex-direction: column;
		background: #442751;
		margin-top: 10px;
		padding: 0 15px;
		list-style: none; }
	&__menu li {
		border-top: 1px solid rgba(#fff, .2);
		display: flex;
		flex-direction: column;
		position: relative; }
	&__menu + li:first-child {
		border: none; }
	&__menu a {
		color: #fff;
		font-size: 14px;
		text-decoration: none;
		padding: 15px 10px; }
	&__menu-link::before {
		content: '';
		position: absolute;
		top: 15px;
		left: 0;
		width: 20px;
		height: 20px;
		background: url(../img/arrow.png) no-repeat center; }
	&__menu-active {
		display: flex; }
	&__submenu {
		display: none;
		flex-direction: column;
		list-style: none;
		padding: 0;
		z-index: 50; }
	&__menu	.header__menu-link > a {
		padding-left: 30px; }
	&__menu-link > .header__submenu {
		display: flex; }
	&__submenu--noactive a {
		color: rgba(#fff, .4); }
	&__submenu a {
		font-size: 13px;
		padding: 10px 70px;
		padding-right: 0;
		padding-bottom: 12px;
		margin: 4px 0; }
	&__submenu a::before {
		content: '';
		position: absolute;
		top: calc(50% - 15px);
		left: 25px;
		width: 30px;
		height: 30px; }
	&__submenu-1 li:first-child a::before {
		background: url(../img/submenu/baby/img1.png) no-repeat center;
		background-size: cover; }
	&__submenu-1 li:nth-child(2) a::before {
		background: url(../img/submenu/baby/img2.png) no-repeat center;
		background-size: cover; }
	&__submenu-1 li:nth-child(3) a::before {
		background: url(../img/submenu/baby/img3.png) no-repeat center;
		background-size: cover; }
	&__submenu-1 li:nth-child(4) a::before {
		background: url(../img/submenu/baby/img4.png) no-repeat center;
		background-size: cover; }
	&__submenu-1 li:nth-child(5) a::before {
		background: url(../img/submenu/baby/img5.png) no-repeat center;
		background-size: cover; }
	&__submenu-1 li:nth-child(6) a::before {
		background: url(../img/submenu/baby/img6.png) no-repeat center;
		background-size: cover; }
	&__submenu-2 li:first-child a::before {
		background: url(../img/submenu/Seats/img1.png) no-repeat center;
		background-size: cover; }
	&__submenu-2 li:nth-child(2) a::before {
		background: url(../img/submenu/Seats/img2.png) no-repeat center;
		background-size: cover; }
	&__submenu-2 li:nth-child(3) a::before {
		background: url(../img/submenu/Seats/img3.png) no-repeat center;
		background-size: cover; }
	&__submenu-2 li:nth-child(4) a::before {
		background: url(../img/submenu/Seats/img4.png) no-repeat center;
		background-size: cover; }
	&__submenu-2 li:nth-child(5) a::before {
		background: url(../img/submenu/Seats/img5.png) no-repeat center;
		background-size: cover; }
	&__submenu-2 li:nth-child(6) a::before {
		background: url(../img/submenu/Seats/img6.png) no-repeat center;
		background-size: cover; }
	&__submenu-3 li:first-child a::before {
		background: url(../img/submenu/Accessories/img1.png) no-repeat center;
		background-size: cover; }
	&__submenu-3 li:nth-child(2) a::before {
		background: url(../img/submenu/Accessories/img2.png) no-repeat center;
		background-size: cover; }
	&__submenu-3 li:nth-child(3) a::before {
		background: url(../img/submenu/Accessories/img3.png) no-repeat center;
		background-size: cover; }
	&__submenu-3 li:nth-child(4) a::before {
		background: url(../img/submenu/Accessories/img4.png) no-repeat center;
		background-size: cover; }
	&__submenu-3 li:nth-child(5) a::before {
		background: url(../img/submenu/Accessories/img5.png) no-repeat center;
		background-size: cover; }
	&__submenu-3 li:nth-child(6) a::before {
		background: url(../img/submenu/Accessories/img6.png) no-repeat center;
		background-size: cover; }
	&__submenu-3 li:nth-child(7) a::before {
		background: url(../img/submenu/Accessories/img7.png) no-repeat center;
		background-size: cover; } }

@media (min-width: 900px) {
	.header {
		position: relative;
		&__wrap img {
			height: 58px; }
		&__form-search {
			display: flex;
			flex-direction: row-reverse;
			position: relative;
			justify-content: flex-end;
			width: 60%;
			height: auto;
			background: transparent;
			margin-left: 5%;
			margin-right: auto; }
		&__form-search span {
			display: none; }
		&__form-search input {
			margin-top: 0;
			cursor: pointer; }
		&__form-search input[type='search'] {
			width: 242px;
			border-top-right-radius: 5px;
			border-bottom-right-radius: 5px;
			transition: .3s; }
		&__form-search input[type='search']:focus {
			width: 70%; }
		&__form-search input[type='submit'] {
			width: 113px;
			border-top-left-radius: 5px;
			border-bottom-left-radius: 5px;
			font-size: 14px;
			color: #fff;
			padding-left: 25px;
			border: none;
			background: #442751 url(../img/search.png) no-repeat 20px center;
			transform: rotate(0deg); }
		&__basket {
			color: #201325;
			font-size: 14px;
			text-decoration: none;
			display: flex;
			align-items: center; }
		&__basket span {
			width: 53px;
			height: 53px;
			background-size: 25px;
			margin-right: 10px; }
		&__wrap-2 {
			display: none; }
		&__menu {
			display: flex;
			flex-direction: row;
			background: #fff;
			margin-top: 40px;
			margin-bottom: 0;
			padding: 0; }
		&__menu > li > a:hover,
		&__menu > li > a:focus {
			background: #FFEFEA; }
		&__menu > li {
			border-top: none;
			width: 20%;
			text-align: center; }
		&__menu a {
			color: #201325;
			font-size: 18px;
			display: flex;
			justify-content: center;
			align-items: center; }
		&__menu > li > a {
			height: 100%;
			border-left: 1px solid #E1DAE5; }
		&__menu > li:first-child > a {
			border: none; }
		&__menu-link::before {
			display: none; }
		&__menu	.header__menu-link > a {
			padding-left: 10px;
			background: #FFEFEA; }
		&__submenu {
			position: absolute;
			top: 100%;
			left: 0;
			background: #FBFDFF;
			box-shadow: 0 7px 10px -13px #000;
			border-bottom-left-radius: 20px;
			border-bottom-right-radius: 20px;
			flex-direction: row;
			justify-content: flex-start;
			align-items: center;
			flex-wrap: wrap;
			padding: 30px 0;
			padding-left: 50px;
			width: 500%;
			z-index: 95; }
		&__submenu-2 {
			margin-left: -100%; }
		&__submenu-3 {
			margin-left: -200%; }
		&__submenu-4 {
			margin-left: -300%; }
		&__submenu-5 {
			margin-left: -400%; }
		&__submenu li {
			width: 33%;
			padding: 20px 40px; }
		&__submenu--noactive a {
			background: #fff;
			color: #D6C6C6;
			cursor: default; }
		&__menu .header__submenu--noactive a:hover,
		&__menu .header__submenu--noactive a:focus {
			background: #fff; }
		&__menu .header__submenu--noactive > a {
			background: #fff; }
		&__submenu a {
			padding-left: 28px;
			font-size: 14px;
			color: #3D3232;
			text-align: left; }
		&__submenu a::before {
			content: '';
			position: absolute;
			top: calc(50% - 26.5px);
			left: 0;
			width: 53px;
			height: 53px; }
		&__overlay {
			display: none;
			position: absolute;
			top: 100%;
			width: 100%;
			height: 100vw;
			background: rgba(#4D4848, .1);
			z-index: 90; }
		&__overlay--active {
			display: block; } } }

@media (min-width: 1300px) {
	.header {
		&__form-search {
			margin-left: 10%; }
		&__form-search input[type='search']:focus {
			width: 80%; }
		&__submenu li {
			width: 20%; } } }
