.news-all-subscribe {
	background: #FFEFEA url(../img/news/img-1.png) no-repeat center 95%;
	background-size: 227px;
	padding: 30px 0;
	padding-bottom: 140px;
	&__block {
		display: flex;
		flex-direction: column;
		margin: auto; }
	&__block h1 {
		text-align: center; }
	&__block p {
		max-width: 506px;
		font-size: 14px;
		margin: 5px auto 20px auto; }
	&__form {
		display: flex;
		flex-direction: column;
		width: 290px;
		margin: auto; }
	&__input {
		font-size: 12px;
		padding: 13px 20px;
		width: 100%;
		background: transparent;
		border-radius: 50px;
		border: 1px solid #B9B9B9;
		margin-bottom: 10px; }
	&__input::placeholder {
		font-size: 12px; }
	&__btn {
		font-size: 12px;
		color: #fff;
		border: none;
		padding: 13px 20px;
		width: 100%;
		background: #442751;
		border-radius: 50px;
		cursor: pointer; }
	&__form label {
		display: flex;
		margin-top: 10px;
		position: relative; }
	&__form label p {
		margin: 0;
		padding-left: 30px;
		font-size: 12px;
		color: rgba(#1D3B3C, .55);
		width: 250px; }
	&__form label p span {
		font-weight: 500;
		text-decoration: underline; }
	&__form input[type="checkbox"] + span {
		position: absolute;
		top: 3px;
		left: 0;
		width: 24px;
		height: 24px;
		border-radius: 50%;
		border: 1px solid #442751; }
	&__form input[type="checkbox"]:checked + span:before {
		content: '';
		position: absolute;
		top: calc(50% - 8px);
		left: calc(50% - 8px);
		width: 16px;
		height: 16px;
		border-radius: 50%;
		background: #442751; } }

@media (min-width: 900px) {
	.news-all-subscribe {
		background: #FFEFEA url(../img/news/img-1.png) no-repeat 95% 20%;
		background-size: 344px;
		padding: 130px 0;
		&__wrap {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-end; }
		&__block {
			margin: 0; }
		&__block h1 {
			text-align: left; }
		&__block p {
			font-size: 18px;
			margin: 0;
			margin-top: 20px; }
		&__form {
			flex-direction: row;
			align-items: flex-end;
			flex-wrap: wrap;
			width: 604px;
			margin: 0;
			margin-top: 20px; }
		&__input {
			font-size: 18px;
			padding: 16px 25px;
			width: 292px;
			margin: 0; }
		&__input::placeholder {
			font-size: 18px; }
		&__btn {
			font-size: 18px;
			padding: 18px 20px;
			width: 292px;
			margin-left: 20px; }
		&__form label {
			margin-top: 0; }
		&__form label p {
			margin: 0;
			padding-top: 5px;
			font-size: 13px;
			width: auto; } } }
