.about-shop-advantages {
	background: #FFEFEA;
	margin-top: 60px;
	padding: 40px 0;
	&__wrap {
		display: flex;
		flex-direction: column; }
	&__list {
		list-style: none;
		padding: 0; }
	&__list li {
		padding-left: 45px;
		position: relative; }
	&__list h3 {
		font-size: 14px; }
	&__list p {
		font-size: 12px; }
	&__list li::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 37px;
		height: 37px;
		border-radius: 50%; }
	&__list li:first-child::before {
		background: #fff url(../img/about-shop/icon-1.png) no-repeat center;
		background-size: 20px; }
	&__list li:nth-child(2)::before {
		background: #fff url(../img/about-shop/icon-2.png) no-repeat center;
		background-size: 20px; }
	&__list li:nth-child(3)::before {
		background: #fff url(../img/about-shop/icon-3.png) no-repeat center;
		background-size: 20px; }
	&__list li:nth-child(4)::before {
		background: #fff url(../img/about-shop/icon-4.png) no-repeat center;
		background-size: 20px; }
	&__photo {
		position: relative;
		margin-top: 20px; }
	&__wrap img {
		width: 100%;
		height: 100%;
		border-radius: 10px; }
	&__photo div {
		content: '';
		position: absolute;
		bottom: 5%;
		right: -10px;
		width: 163px;
		height: 63px;
		padding: 8px 15px;
		padding-right: 0;
		border-radius: 10px;
		background: #FFEFEA; }
	&__photo h3 {
		font-size: 12px;
		margin: 0; }
	&__photo p {
		font-size: 10px;
		margin: 0; } }

@media (min-width: 900px) {
	.about-shop-advantages {
		margin-top: 60px;
		padding: 140px 0;
		&__wrap {
			flex-direction: row;
			justify-content: space-between;
			flex-wrap: wrap; }
		&__wrap div {
			width: 48%; }
		&__list li {
			margin-bottom: 60px;
			padding-left: 80px; }
		&__list li:first-child {
			margin-top: 70px; }
		&__list h3 {
			font-size: 18px; }
		&__list p {
			font-size: 16px; }
		&__list li::before {
			width: 70px;
			height: 70px; }
		&__list li:first-child::before {
			background-size: auto; }
		&__list li:nth-child(2)::before {
			background-size: auto; }
		&__list li:nth-child(3)::before {
			background-size: auto; }
		&__list li:nth-child(4)::before {
			background-size: auto; }
		&__photo {
			margin-top: 6px;
			min-width: 600px;
			display: flex;
			justify-content: flex-end;
			max-width: 634px;
			max-height: 700px; }
		&__photo div {
			content: '';
			position: absolute;
			bottom: 5%;
			right: -10px;
			width: 300px;
			height: 105px;
			padding: 19px 25px;
			border-radius: 10px;
			background: #FFEFEA; }
		&__photo h3 {
			font-size: 16px; }
		&__photo p {
			font-size: 16px; } } }
