.news-all-pagination {
    margin: 30px 0; }

ul.news-all-pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: auto;
    width: 290px; }

ul.news-all-pagination li {
    display: inline; }

ul.news-all-pagination li a {
    color: #442751;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
    transition: background-color .3s;
    border: 1px solid #EFEBF2;
    font-size: 14px; }

ul.news-all-pagination li:first-child a {
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px; }

ul.news-all-pagination li:last-child a {
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px; }

ul.news-all-pagination li a.active {
    background-color: #442751;
    color: white;
    border: 1px solid #442751; }

ul.news-all-pagination li a:hover:not(.active) {
    background-color: #ddd; }

@media (min-width: 900px) {
    .news-all-pagination {
        margin: 50px 0;
        margin-bottom: 100px; }

    ul.news-all-pagination li a {
        font-size: 18px;
        padding: 10px 25px; } }
