.warranty-warranty {
	background: #FFEFEA;
	padding-top: 40px;
	padding-bottom: 60px;
	&__text {
		font-size: 14px; }
	&__text-toggle {
		text-decoration: none;
		font-size: 12px;
		color: #8E8E8E; }
	&__text--off {
		display: none; }
	&__text--on {
		display: block; }
	&__contacts {
		margin-top: 30px; }
	&__list {
		list-style: none;
		padding: 0;
		margin-top: 30px;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }
	&__list li {
		background: #fff;
		position: relative;
		padding: 20px;
		padding-left: 70px;
		border-radius: 10px;
		margin-bottom: 10px;
		width: 290px;
		margin-right: 10px; }
	&__list span {
		position: absolute;
		top: calc(50% - 27px);
		left: 10px;
		width: 54px;
		height: 54px;
		border: 10px solid #FFEFEA;
		border-radius: 50%; }
	&__list li:nth-child(1) span {
		background: url(../img/placeholder.png) no-repeat center;
		background-size: 23px; }
	&__list li:nth-child(2) span {
		background: url(../img/phone-call.png) no-repeat center; }
	&__list li:nth-child(3) span {
		background: url(../img/email.png) no-repeat center;
		background-size: 29px; }
	&__list h3 {
		font-size: 14px;
		margin: 0; }
	&__title {
		font-size: 16px; }
	&__list p {
		font-size: 12px;
		margin: 0; }
	&__list a {
		display: block;
		text-decoration: none;
		font-size: 12px;
		color: #413D43;
		margin: 0; }
	&__wrap iframe {
		width: 100%;
		height: 376px; } }

@media (min-width: 1000px) {
	.warranty-warranty {
		padding-top: 150px;
		padding-bottom: 150px;
		&__wrap {
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap; }
		&__block {
			width: 55%; }
		&__wrap iframe {
			width: 40%;
			height: 607px; }
		&__text {
			font-size: 18px; }
		&__text-toggle {
			display: none; }
		&__text--off {
			display: block; }
		&__list {
			justify-content: stretch; }
		&__list li {
			padding: 30px;
			padding-left: 80px;
			width: 50%;
			margin-right: 0; }
		&__list li:first-child {
			width: 100%; }
		&__list li:nth-child(2) {
			border-top-right-radius: 0;
			border-bottom-right-radius: 0; }
		&__list li:nth-child(3) {
			border-top-left-radius: 0;
			border-bottom-left-radius: 0; }
		&__list span {
			top: calc(50% - 30px);
			width: 60px;
			height: 60px; }
		&__list li:nth-child(1) span {
			background: url(../img/placeholder.png) no-repeat center;
			background-size: 23px; }
		&__list li:nth-child(2) span {
			background: url(../img/phone-call.png) no-repeat center; }
		&__list li:nth-child(3) span {
			background: url(../img/email.png) no-repeat center;
			background-size: 33px; }
		&__list h3 {
			font-size: 16px; }
		&__title {
			font-size: 20px; }
		&__list p {
			font-size: 16px; }
		&__list a {
			font-size: 16px; } } }
