.about-shop-contacts {
	margin: 30px 0;
	&__list {
		list-style: none;
		padding: 0;
		margin-top: 25px; }
	&__list li {
		padding: 20px;
		padding-left: 70px;
		margin-top: 10px;
		border-radius: 10px;
		max-width: 433px; }
	&__list h3 {
		font-size: 14px;
		margin: 0; }
	&__list p {
		font-size: 12px;
		margin: 0;
		margin-top: 6px; }
	&__list li:first-child {
		background: #fff url(../img/about-us/img1.png) no-repeat 10px center;
		background-size: 53px; }
	&__list li:nth-child(2) {
		background: #fff url(../img/about-us/img2.png) no-repeat 10px center;
		background-size: 53px; }
	&__list li:nth-child(3) {
		background: #fff url(../img/about-us/img3.png) no-repeat 10px center;
		background-size: 53px; } }

@media (min-width: 900px) {
	.about-shop-contacts {
		margin-top: 130px;
		&__wrap h1 {
			text-align: center; }
		&__list {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between; }
		&__list li {
			padding: 30px;
			padding-left: 128px;
			width: 433PX;
			margin-left: 10px; }
		&__list h3 {
			font-size: 16px; }
		&__list p {
			font-size: 16px;
			margin-top: 10px; }
		&__list li:first-child {
			background: #fff url(../img/about-us/img1.png) no-repeat 30px center;
			background-size: 78px;
			margin-left: 0; }
		&__list li:nth-child(2) {
			background: #fff url(../img/about-us/img2.png) no-repeat 30px center;
			background-size: 78px; }
		&__list li:nth-child(3) {
			background: #fff url(../img/about-us/img3.png) no-repeat 30px center;
			background-size: 78px;
			margin-top: 10px; } } }
