.request-accepted {
	position: fixed;
	width: 290px;
	height: 194px;
	top: calc(50% - 97px);
	left: calc(50% - 145px);
	background: #FFEFEA url(../img/request-accepted.png) no-repeat 105% 100px;
	box-shadow: 0 5px 10px 1px rgba(#442751, 0.3);
	background-size: 160px;
	border-radius: 10px;
	z-index: 102;
	display: none;
	&--active {
		display: block; }
	&__wrap {
		padding: 20px;
		padding-bottom: 30px;
		display: flex;
		flex-direction: column; }
	&__btn-close {
		width: 20px;
		height: 20px;
		padding: 0;
		border: none;
		background: transparent;
		position: relative;
		margin-left: auto; }
	&__btn-close::before,
	&__btn-close::after {
		content: '';
		position: absolute;
		top: 9px;
		width: 20px;
		height: 1px;
		background: #442751; }
	&__btn-close::before {
		right: 0;
		transform: rotate(45deg); }
	&__btn-close::after {
		left: 0;
		transform: rotate(-45deg); }
	&__wrap p {
		font-size: 14px;
		margin-top: 5px; } }

@media (min-width: 900px) {
	.request-accepted {
		width: 632px;
		height: 300px;
		top: calc(50% - 150px);
		left: calc(50% - 316px);
		background: #FFEFEA url(../img/request-accepted.png) no-repeat 90% 100px;
		background-size: auto;
		&__wrap {
			padding-left: 40px; }
		&__wrap p {
			font-size: 18px; } } }
