.product-card {
	&__list {
		list-style: none;
		padding: 0;
		display: flex;
		flex-wrap: wrap; }
	&__list li {
		position: relative;
		width: 49%;
		max-width: 240px;
		padding: 15px 10px;
		margin auto {}
		margin-bottom: 10px;
		background: #fff;
		border-radius: 10px; }
	&__product {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0; }
	&__list li > img {
		width: 100%; }
	&__title {
		font-size: 10px;
		color: #201325;
		font-weight: 600; }
	&__block-price {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__price {
		font-size: 14px;
		font-weight: bold;
		color: #442751;
		margin: 0; }
	&__basket {
		width: 29px;
		height: 29px;
		border-radius: 50%;
		background: #FFEBE6 url(../img/bag-purple.png) no-repeat center;
		background-size: 15px; }
	&__bottom-panel {
		display: none; }
	&__sidebar {
		display: none; } }

@media (min-width: 900px) {
	.product-card {
		&__list li {
			position: relative;
			width: 24%;
			margin: 0;
			margin-right: 1%;
			max-width: 390px;
			padding: 20px 30px;
			margin-bottom: 20px; }
		&__title {
			font-size: 18px;
			margin: 5px 0; }
		&__price {
			font-size: 24px; }
		&__basket {
			width: 53px;
			height: 53px;
			background-size: auto; }
		&__list li:hover .product-card__bottom-panel {
			position: absolute;
			top: calc(100% - 10px);
			left: 0;
			display: flex;
			height: 210px;
			flex-direction: column;
			background: #fff;
			border-bottom-right-radius: 10px;
			z-index: 100; }
		&__list li:nth-child(4n+1) .product-card__bottom-panel {
			border-bottom-right-radius: none;
			border-bottom-left-radius: 10px; }
		&__available {
			border-top: 1px solid #E1DAE5;
			border-bottom: 1px solid #E1DAE5;
			padding: 10px 0;
			padding-left: 30px; }
		&__available h3 {
			display: flex;
			align-items: center;
			font-size: 14px;
			color: #413D43;
			margin: 0; }
		&__available span {
			width: 16px;
			height: 16px;
			margin-right: 10px;
			background: url(../img/product-card/available.svg); }
		&__available p {
			font-size: 12px;
			color: #8E8E8E;
			margin: 0;
			margin-top: 3px; }
		&__characteristic {
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding: 10px 30px; }
		&__characteristic p {
			font-size: 12px;
			margin-top: 0;
			margin-bottom: 10px; }
		&__list li:hover .product-card__sidebar {
			position: absolute;
			top: 0;
			right: calc(100% - 10px);
			display: flex;
			flex-direction: column;
			align-items: center;
			height: calc(100% + 200px);
			width: 90px;
			background: #fff;
			border-right: 1px solid #E1DAE5;
			border-top-left-radius: 10px;
			border-bottom-left-radius: 10px;
			z-index: 100; }
		&__list li:nth-child(4n+1) .product-card__sidebar {
			left: calc(100% - 10px);
			border-radius: 0;
			border-top-right-radius: 10px;
			border-bottom-right-radius: 10px;
			border-right: none;
			border-left: 2px solid #E1DAE5; }
		&__sidebar div {
			position: relative;
			width: 74px;
			height: 74px;
			border-radius: 10px;
			margin-top: 10px;
			border: 2px solid #FBF4FF;
			cursor: pointer; }
		&__active::before {
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			border-radius: 10px;
			background: rgba(#EADDF1, .4);
			border: 1px solid #EADDF1; } } }
