.about-shop-assortment {
	margin: 30px 0;
	&__wrap {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start; }
	&__block-text {
		margin-bottom: 30px; }
	&__block-text p {
		font-size: 14px;
		display: none; }
	&__block-text p:nth-child(2) {
		display: block; }
	&__block-text .about-shop-assortment__text-on {
		display: block; }
	&__toggle-text {
		font-size: 14px;
		color: #8E8E8E;
		text-decoration: none; }
	&__wrap img {
		width: 100%;
		max-width: 757px;
		border-radius: 10px; } }

@media (min-width: 900px) {
	.about-shop-assortment {
		margin: 130px 0;
		&__wrap {
			flex-direction: row;
			justify-content: space-between; }
		&__block-text {
			width: 50%;
			max-width: 673px; }
		&__block-text p {
			font-size: 18px;
			display: block; }
		&__toggle-text {
			display: none; }
		&__wrap img {
			width: 45%; } } }
